<!-- 发现主页 -->
<template>
  <div class="discover">
    <div class="main-content">
      <res-search @handleSearch='handleSearch' v-model="searchValue"></res-search>
      <!-- 学校 -->
      <template>
        <div  v-if="id==0" class="search-type">
          <div class="area">
            <span v-clickOutSide="handleHideAll" @click="dropObj.org = true;dropObj.eduArea = false;dropObj.schoolSelect=false">机构: 
              {{orgText}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <div v-if="dropObj.org" class="area-lists">
              <el-scrollbar style="height:100%">
                <el-tree :data="orgLists" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
              </el-scrollbar>
            </div>
          </div>
          <div class="district">
            <span v-clickOutSide="handleHideAll" @click="dropObj.eduArea = true;dropObj.org = false;dropObj.schoolSelect=false"> 
              学区: {{eduAreaText}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <div v-if="dropObj.eduArea">
              <el-scrollbar style="height:100%">
                <div @click="handleSelecteduArea({id:0,name:'不限'})" class="item text-overflow">不限</div>
                <div @click="handleSelecteduArea(item)" v-for="item in eduAreaList" :key="item.id" class="item text-overflow">
                  {{item.name}}
                </div>
              </el-scrollbar>
            </div>
          </div>
          <div v-if="navSelect === 'teacher'" class="school-select">
            <span @click.stop="dropObj.eduArea = false;dropObj.org = false;dropObj.schoolSelect=true">
              学校:{{schoolText}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <div  v-clickOutSide="handleHideAll" v-if="dropObj.schoolSelect" class="scholl-list-wrap">
              <div class="search-input">
                <el-input 
                suffix-icon="el-icon-search"
                v-model="searchSchool"></el-input>
              </div>
              <div class="scholl-list">
                <el-scrollbar style="height:100%">
                  <div @click="handleSelectSchool({id:0,displayName:'不限'})"  class="item">不限</div>
                  <div @click="handleSelectSchool(item)" :key="item.id" v-for="item in schoolList" class="item">{{item.displayName}}</div>
                </el-scrollbar>
              </div>
            </div>
          </div>
        </div>
      </template>

      <div class="res-boxs">
        <template v-if="navSelect === 'school'">
          <res-school @click.native="handleTurnToSchool(item)" :info="item" v-for="item in schoolData" :key="item.id"></res-school>
        </template>
        <template v-else>
          <res-teacher :info="item" v-for="item in teacherData" :key="item.id"></res-teacher>
        </template>
      </div>
        <resPagination 
        v-if="pagination.total / pagination.pageSize > 1"
          :currentPage="pagination.currentPage" 
          :total="pagination.total"
          :pageSize="pagination.pageSize"
          @onSizeChange="handlePageChange"
        ></resPagination>
    </div>
    <res-footer></res-footer>

    <!-- 侧边栏 -->
    <div class="nav">

      <div v-if="id==0" @click="handleSelectNav('school')" :class="{active:navSelect === 'school'}">
        <i class="icon-solid-shcool iconfont"></i>
        <p>学校</p>
      </div>

      <div @click="handleSelectNav('teacher')" :class="{active:navSelect === 'teacher'}">
        <i class="icon-solid-user iconfont"></i>
        <p>教师</p>
      </div>

    </div>
  </div>
</template>

<script>
export default {
    components: {
        resSearch: () => import("@/components/res-search"),
        resFooter: () => import("@/components/res-footer"),
        resPagination: () => import("@/components/res-pagination"),
        resSchool: () => import("@/components/res-school"),
        resTeacher: () => import("@/components/res-teacher")
    },
    data() {
        return {
          id:0,
          searchSchool:'',
          schoolList:[],
          schoolData:[],
          teacherData:[],
          orgLists:[],
          eduAreaList:[],
          /* nav导航 school teacher*/
          navSelect: 'school',
          /* 下拉控制显示 */
          dropObj: {
            org: false,
            eduArea: false,
            schoolSelect:false
          },
          defaultProps: {
            children: 'children',
            label: 'text'
          },
          orgText:"不限",
          orgId: 0,
          eduAreaText: "不限",
          eduAreaId:0,
          schoolId:0,
          schoolText:"不限",
          searchValue:'',
          pagination:{
            currentPage:1,
            total:20,
            pageSize:25,
          }
        }
    },
    created(){
      this.id = this.$route.query.id || 0;
      if(this.id != 0) {
        this.schoolId = this.id
        this.navSelect = "teacher"
        this.handleSearch()
      } else {
         this.selectSchoolPageData();
      }
      this.initOrg();
     
    },
    watch:{
      changeOrgOrAreaId(val){
        this.pagination.currentPage = 1;
        if(this.navSelect === "school") {
          this.selectSchoolPageData()
        } else {
          this.selectTeacherPageData()
          /* 教师 */
          const {eduAreaId,orgId} = val;
          if(eduAreaId!==0&&orgId!==0) {
            this.selectSchool()
          }
        }
      },
      searchSchool(newValue){
        this.filterSchool()
        if(!newValue){
          this.schoolList = [...this.schoolListsCopy]
        }
      }
    },
    computed:{
      changeOrgOrAreaId(){
        return {
          orgId:this.orgId,
          eduAreaId:this.eduAreaId
        }
      }
    },
    methods: {
      /* 学校 */
      selectSchoolPageData(){
        const params = {
          schoolName:this.searchValue,
          pageSize:this.pagination.pageSize,
          orgId:this.orgId,
          eduAreaId:this.eduAreaId,
          currPage : this.pagination.currentPage
        }
        this.$api.selectSchoolPageData(params).then(res => {
          this.schoolData = res.data
          this.pagination.total = Number(res.totalDatas)
        })
      },
      /* 教师 */
      selectTeacherPageData(){
        const params = {
          teacherName:this.searchValue,
          schoolId:this.schoolId,
          pageSize:this.pagination.pageSize,
          orgId:this.orgId,
          eduAreaId:this.eduAreaId,
          currPage : this.pagination.currentPage
        }
        this.$api.selectTeacherPageData(params).then(res => {
          this.teacherData = res.data
          this.pagination.total = Number(res.totalDatas)
        })
      },
      /* 通过学区 机构 查找学校 */
      selectSchool(){
        const params = {
          eduAreaId:this.eduAreaId,
          orgId:this.orgId
        }
        this.$api.selectSchool(params).then(res => {
          this.schoolList = res.data;
          this.schoolListsCopy = [...res.data]
        })
      },
      handleSelectSchool(item){
        this.schoolText = item.displayName;
        this.schoolId = item.id;
        this.pagination.currentPage = 1;
        this.selectTeacherPageData();
        this.handleHideAll();
      },
      initOrg(){
        this.$api.selectOrgs().then(res => {
          this.orgLists = res.data;
          this.orgLists.unshift({id:0,text:'不限'})
        })
      },
      /* 获取学区通过ID */
      selectEduAreaByCity(){
        this.$api.selectEduAreaByCity(this.orgId).then(res => {
          this.eduAreaList = res.data;
        })
      },
      filterSchool(){
        if(this.schoolList.length === 0) return;
        this.schoolList = this.schoolList.filter(item => {
          return item.displayName.indexOf(this.searchSchool) !== -1;
        })
      },
      handleNodeClick(data){
        if(data.children) return;
        this.orgId = data.id;
        this.orgText = data.text;
        this.eduAreaList = [];
        this.eduAreaId = 0;
        this.eduAreaText = "不限"
        this.schoolList = [];
        this.schoolId = 0;
        this.schoolText = "不限"
        this.handleHideAll();
        this.selectEduAreaByCity();
      },
      handleSelecteduArea(obj){
        this.eduAreaId = obj.id;
        this.eduAreaText = obj.name;
        this.schoolList = [];
        this.schoolId = 0;
        this.schoolText = "不限"
        this.handleHideAll();
        this.selectSchool();
      },
      handleHideAll(){
        this.dropObj = {
          org: false,
          eduArea:false,
          schoolSelect:false
        };
      },
      handlePageChange(currentPage){
        this.pagination.currentPage = currentPage;
        if(this.navSelect === 'school') {
          this.selectSchoolPageData();
        } else {
          this.selectTeacherPageData();
        }
      },
      handleSearch(){
        this.pagination.currentPage = 1;
        if(this.navSelect === 'school') {
          this.selectSchoolPageData();
        } else {
          this.selectTeacherPageData();
        }
      },
        /* 重置搜索条件 */
        reset(){
          this.orgId = 0;
          this.eduAreaId = 0;
          this.schoolId = 0;
          this.orgText = "不限";
          this.eduAreaText = "不限";
          this.schoolText =  "不限";
          this.pagination.currentPage = 1;
          this.searchValue = '';
        },
        // nav 选择
        handleSelectNav(type) {
          this.navSelect = type;
          this.reset();
          if(this.navSelect === 'school') {
            this.selectSchoolPageData();
          } else {
            this.selectTeacherPageData();
          }
        },
        /* 选择school 跳转 */
        handleTurnToSchool(item) {
          const link = this.$router.resolve({
              path:"/resource/Layout/index",
              query: {
                id:item.id
              }
          });
          window.open(link.href, '_blank');
        }
    }
};
</script>

<style lang="scss" scoped="scoped">
  .discover {
    min-width: 1200px;
    background: #f5f6f8;
    overflow: hidden;
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .search-type {
      width: 80%;
      margin: 0 auto;
      display: flex;
      span {
        cursor: pointer;
      }
      .area {
        min-width: 120px;
        position: relative;
        .area-lists {
          z-index: 9999;
          width: 240px;
          position: absolute;
          left: 0;
          top: 30px;
          border-radius: 4px;
          overflow: hidden;
          box-shadow: 0 0 7.2px 0.8px rgba(0, 0, 0, .1);
        }
      }
      .district {
        min-width: 120px;
        position: relative;
        margin-left: 20px;
      }
      .district > div {
        width: 128px;
        // height: 196px;
        position: absolute;
        top: 30px;
        left: 0;
        background: #fff;
        overflow: hidden;
        z-index: 998;
        border-radius: 4px;
        box-shadow: 0 0 7.2px 0.8px rgba(0, 0, 0, .1);
      }
      .district > div .item {
        cursor: pointer;
        padding-left: 10px;
        line-height: 36px;
      }
      .district > div .item:hover {
        background: #f5f6f9;
        color: #2695ff
      }
      .school-select {
        position: relative;
        .scholl-list-wrap {
          position: absolute;
          left: 0;
          top: 30px;
          background: #fff;
          width: 220px; 
          z-index: 999;
          border-radius: 4px;
          box-shadow: 0 0 7.2px 0.8px rgba(0, 0, 0, .1);
        }
        .search-input {
          padding: 5px;
        }
        .scholl-list {
          height: 200px;
          overflow: hidden;
          padding-top: 5px;
          .item {
            padding-left:10px;
            height: 36px;
            line-height: 36px;
            color: #55575B;
            cursor: pointer;
            &:hover {
              background: #F5F6F9
            }
          }
        }
      }
    }
    .res-boxs {
      width: 81.6%;
      margin: 0 auto;
      margin-top: 20px;
      display: flex;
      flex-wrap: wrap;
      & > div {
        width: 25%;
      }
      @media screen and (min-width: 1520px) {
        & > div {
          width: 20%;
        }
      }
    }
  }
  .nav {
    position: fixed;
    left: 0;
    top: 60px;
    bottom: 0;
    width: 80px;
    background: #181A1E;
  }
  .nav > div {
    text-align: center;
    color: #A9B6CB;
    cursor: pointer;
    margin: 15px 0;
    height: 80px
  }
  .nav > div p {
    color: #A9B6CB
  }
  .nav > div.active {
    i, p {
      color: #2695FF;
    }
  }
  .nav > div i {
    font-size: 30px;
  }
</style>
<style>
.discover .el-tree {
  background: #fff;
}
.discover .search-input .el-input__icon {
  line-height: 37px;
  font-size: 18px;
}
</style>